export default {
	data() {
		return {
			type: 1, //1查询我的买单 2查询我卖的
			mainItem: [],
			status: 0, //类别
			user_id: 0,
			statusItem: [{
				id: 0,
				name: '全部'
			}, {
				id: 1,
				name: '待付款'
			}, {
				id: 2,
				name: '待确定'
			}, {
				id: 3,
				name: '已确定'
			}, {
				id: 4,
				name: '已完成'
			}, {
				id: 5,
				name: '已取消'
			}],
			loadMoreText: '加载中...',
			showLoadMore: false,
			isMore: true,
			PageNo: 1,
			PageSize: 10
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		if (option.type != '') this.type = option.type;
		var user = uni.getStorageSync('uerInfo');
		this.user_id = user.id;
		if (this.type == 2) { //卖单
			this.statusItem.push({
				id: 6,
				name: '待被买'
			})
		}
		this.getPage();
	},
	onUnload() {
		(this.PageNo = 1), (this.mainItem = []), (this.loadMoreText = '加载更多'), (this.showLoadMore = false);
	},
	onReachBottom() {
		//下拉加载
		this.showLoadMore = true;
		setTimeout(() => {
			this.setListData();
		}, 300);
	},
	onPullDownRefresh() {
		//上拉刷新
		this.initData();
	},
	methods: {
		selType(status) {
			//选择类别
			this.status = status;
			this.initData();
		},
		dump(url) {
			uni.navigateTo({
				url: url
			});
		},
		withdrawal() {
			//提现
			uni.navigateTo({
				url: '/pages/memberAccount/withdrawal'
			});
		},
		initData() {
			//刷新数据
			var _this = this;
			setTimeout(() => {
				_this.PageNo = 1;
				_this.mainItem = [];

				_this.getPage();
				uni.stopPullDownRefresh();
			}, 300);
		},
		setListData() {
			//加载更多数据
			this.PageNo += 1;
			if (this.isMore) this.getPage();
		},
		getPage() {
			//加载数据
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.order
				.orderlist({
					page: _this.PageNo,
					type: _this.type,
					order_state: _this.status
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.mainItem = _this.mainItem.concat(data.data);
						if (data.data.length < _this.PageSize) {
							this.loadMoreText = '没有更多数据了';
							_this.isMore = false;
						} else {
							_this.showLoadMore = false;
							_this.isMore = true;
						}
					} else {
						_this.showLoadMore = false;
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					_this.showLoadMore = false;
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
